.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #101719;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h2 {
  font-weight: 700;
}

h5 {
  font-weight: 300;
  font-size: 1.375rem;
}

.primary-text {
  font-weight: 700;
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.sub-primary-text {
  font-weight: 700;
  font-size: 1.75rem;
  margin-bottom: 0.25rem;
}

.secondary-text {
  font-weight: 300;
  font-size: 1.375rem;
}
